      height = heightBasis;

    this.donutTarget.selectAll('g').remove();

    var donutData = d3
      .nest()
      .key(function (d) { return d.targtype1_txt; })
      .rollup(d => d.length)
      .entries(this.patchedUpChartsData)
      .sort((a, b) => b.value - a.value);

    // a circle chart needs a radius
    var radius = width / 4 - margin.left;
